.card {
  width: 440px;
  height: 520px;
  margin-bottom: 80px;
  margin-right: 80px;
  margin-left: 80px;
  border-radius: 5% !important;
  transition: transform 450ms ease-in-out !important;
}

.MuiButton-contained {
  color: white !important;
  margin-bottom: 5% !important;
  border-top-right-radius: 35% !important;
  border-bottom-left-radius: 35% !important;
  padding: 2.5% !important;
  font-family: "Dosis", sans-serif !important;
  background-color: #a8e619 !important;
  transition: all 450ms linear;
}

p.MuiTypography-root {
  color: #21333b !important;
  line-height: 1.4rem !important;
  font-family: "Dosis", sans-serif;
  font-size: 1.15em !important;
  border-radius: 10% !important;
}

ul.tech_list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-left: 0;
  margin-bottom: 0;
}

.tech_list > li {
  margin-right: 10px;
  font-size: 1.1em;
}

.MuiCardActions-root {
  justify-content: center !important;
}

ul.tech_list {
  margin-top: 0;
  color: #21333b;
  font-family: "Dosis", sans-serif;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .card {
    width: 90vw;
    height: auto;
    border-radius: 5% !important;
    margin-left: auto;
    margin-right: auto;
  }
}

@media only screen and (min-width: 480px) and (max-width: 954px) {
  .card {
    width: 60vw;
    height: auto;
    margin-left: auto;
    margin-right: auto;
  }
}
